import React from 'react';
import { Route, Routes } from 'react-router-dom';

const ContactList = React.lazy(() => import('./ContactList/Index'));
const ContactDetails = React.lazy(() => import('./ContactDetails/ContactDetails'));
const AddEditContact = React.lazy(() => import('./AddContact/AddContact'));

const ContactRouter = () => (
  <Routes>
    <Route
      path=""
      element={(<ContactList />)}
    />
    <Route
      path="details/:id"
      element={(<ContactDetails />)}
    />
    <Route
      path="create"
      element={(<AddEditContact />)}
    />
    <Route
      path="edit"
      element={(<AddEditContact />)}
    />
  </Routes>
);

export default ContactRouter;
